export default class Gallery {
    constructor(image) {
        this.image = image
    }

    show () {
        this.insert()
        this.getImagesFromServer()
    }

    hide () {
        this.$gallery.remove()
        this.$input = null
        this.$images = null
        this.$close = null
        this.$loader = null
        this.$imagesContainer = null
    }

    insert (images = []) {
        document.querySelectorAll('div[data-gallery=""]').forEach((gallery) => gallery.remove())
        document.body.insertAdjacentHTML('beforeend', this.getTemplate(this.getImagesTemplate(images)))
        this.$gallery = document.querySelector('div[data-gallery=""]')
        this.$input = document.querySelector('input[data-gallery="input"]')
        this.$images = document.querySelectorAll('img[data-gallery="image"]')
        this.$imagesContainer = document.querySelector('div[data-gallery="images"]')
        this.$close = document.querySelector('button[data-gallery="close"]')
        this.$loader = document.querySelector('div[data-gallery="loader"]')
    }

    toggleLoader (state) {
        this.$loader.style.display = state ? 'none' : 'block'
    }

    bindToolbarEvents () {
        this.$input.addEventListener('blur', (event) => this.getImagesFromServer())
        this.$close.addEventListener('click', (event) => this.hide())
        this.$images.forEach((image) => {
            image.addEventListener('click', () => {
                this.image.setNodeValue(image.src)
                this.hide()
            })
        })
    }

    getImagesFromServer() {
        this.$imagesContainer.remove()
        this.toggleLoader(false)

        const endpoint = '/constructor/php/images.php'
        const path = this.$input.value

        const request = new Request(endpoint, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({path}),
        })
        fetch(request).then((response) => {
            response.json().then((response) => setTimeout(() => this.updateImages(JSON.parse(response)), 1000))
        }).catch((error) => {
            error.json().then((error) => console.log('    .'))
        })
    }

    updateImages(images) {
        this.insert(images)
        this.bindToolbarEvents()
    }

    getImagesTemplate(images) {
        return images.reduce((acc, image) => {
            return acc + `
                <div class="constructor-gallery__image">
                    <img src="${image}" alt="" data-gallery="image">
                </div>
            `
        }, '')
    }

    getTemplate (images = '') {
        return `
        <div id="constructor-gallery" data-gallery style="display: block;">
            <div class="constructor-gallery__input">
                <label for="">  </label>
                <input type="text" value="/images/" placeholder="   " data-gallery="input">
            </div>
            <div class="constructor-gallery__loader" data-gallery="loader" style="display: none"> ...</div>
            <div class="constructor-gallery__images" data-gallery="images">
                ${images}
            </div>
            <div class="constructor-gallery__footer">
                <button class="btn btn-primary" data-gallery="close"></button>
            </div>
        </div>
        `
    }
}
