export default class Recorder {
  constructor (app) {
    this.app = app
  }

  download(newHtml) {
    const endpoint = endpoint || this.app.config.defaultEndpoint

    const request = new Request(endpoint, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        ...this.app.config.headers,
      },
      body: JSON.stringify({newHtml}),
    })

    fetch(request).then((response) => {
      response.json().then((response) => {
        this.app.debug('Recorder response', response)
        const link = document.createElement('a');
        link.setAttribute('href','/constructor/output/index.php');
        link.setAttribute('download', 'index.php');
        link.click();
      })
    }).catch((error) => {
      error.json().then((response) => this.app.debug('Recorder error', response))
    })
  }

  save (changed, endpoint = null) {
    this.app.debug('Recorder saving', changed)
    endpoint = endpoint || this.app.config.defaultEndpoint

    const request = new Request(endpoint, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        ...this.app.config.headers,
      },
      body: JSON.stringify(changed),
    })

    fetch(request)
        .then((response) => {
      response.json().then((response) => {
        this.app.debug('Recorder response', response)
      })
    }).catch((error) => {
      error.json().then((response) => this.app.debug('Recorder error', response))
    })
  }
}
